.background {
  width: 100%;
  height: 73.5625rem;
  top: -40.3125rem;
  left: 17.6875rem;
  border-radius: 50%;
  background: var(--light-green);
}

:root {
  --main--green: hsla(103, 65%, 45%, 1);
  --light-green: hsla(103, 100%, 94%, 1);
  --primary-color: hsl(0, 0%, 100%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  width: 80rem;
  height: 49.375rem;
  overflow: hidden;
}